"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { ArrowLeft, Package, Download } from "lucide-react"
import * as XLSX from 'xlsx'

interface EquipmentWithdrawal {
  request_id: string
  created_at: string
  description_price: string
  cost: string
  username: string
  status: number
  type_of_work: string
  work: string
  site: string
}

const statusColors = {
  0: "bg-yellow-100 text-yellow-800 border-yellow-300",
  1: "bg-blue-100 text-blue-800 border-blue-300",
  2: "bg-green-100 text-green-800 border-green-300",
  3: "bg-red-100 text-red-800 border-red-300",
  4: "bg-orange-100 text-orange-800 border-orange-300",
}

const statusText = {
  0: "รอดำเนินการ",
  1: "กำลังดำเนินการ",
  2: "เสร็จสิ้น",
  3: "ยกเลิก",
  4: "รอการประเมิน",
}

export default function EquipmentPage() {
  const router = useRouter()
  const [equipmentList, setEquipmentList] = useState<EquipmentWithdrawal[]>([])
  const [loading, setLoading] = useState(true)
  const [isAdmin, setIsAdmin] = useState(false)

  useEffect(() => {
    // Check authentication
    const userStr = localStorage.getItem("user")
    if (!userStr) {
      router.push('/login')
      return
    }

    const user = JSON.parse(userStr)
    if (user.role !== "admin") {
      router.push('/dashboard')
      return
    }

    setIsAdmin(true)
    fetchEquipmentData()
  }, [router])

  const fetchEquipmentData = async () => {
    try {
      setLoading(true)
      const response = await fetch('/api/tickets')
      if (response.ok) {
        const data = await response.json()
        // Filter only tickets with price_type = 0 (เบิกจ่าย)
        const withdrawals = data.filter((ticket: any) => 
          ticket.price_type === 0 && 
          ticket.cost && 
          ticket.cost !== "0"
        )
        setEquipmentList(withdrawals)
      }
    } catch (error) {
      console.error('Failed to fetch equipment data:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleExportExcel = () => {
    const exportData = equipmentList.map((item, index) => ({
      'ลำดับ': index + 1,
      'เลขที่รายการ': item.request_id,
      'วันที่': new Date(item.created_at).toLocaleDateString('th-TH'),
      'อุปกรณ์': item.description_price,
      'ราคา (บาท)': item.cost,
      'ผู้แจ้ง': item.username,
      'สาขา': item.site || '-',
      'ประเภทงาน': item.type_of_work || '-',
      'ชนิดงาน': item.work || '-',
      'สถานะ': statusText[item.status as keyof typeof statusText],
    }))

    const ws = XLSX.utils.json_to_sheet(exportData)
    const wb = XLSX.utils.book_new()
    XLSX.utils.book_append_sheet(wb, ws, 'รายการอุปกรณ์เบิกจ่าย')
    
    // Set column widths
    ws['!cols'] = [
      { wch: 8 },  // ลำดับ
      { wch: 15 }, // เลขที่รายการ
      { wch: 15 }, // วันที่
      { wch: 25 }, // อุปกรณ์
      { wch: 12 }, // ราคา
      { wch: 20 }, // ผู้แจ้ง
      { wch: 15 }, // สาขา
      { wch: 15 }, // ประเภทงาน
      { wch: 15 }, // ชนิดงาน
      { wch: 15 }, // สถานะ
    ]

    const date = new Date().toLocaleDateString('th-TH')
    XLSX.writeFile(wb, `รายการอุปกรณ์เบิกจ่าย_${date}.xlsx`)
  }

  const calculateTotalCost = () => {
    return equipmentList.reduce((sum, item) => {
      const cost = parseFloat(item.cost) || 0
      return sum + cost
    }, 0)
  }

  if (!isAdmin) {
    return null
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      <div className="container mx-auto p-4 max-w-7xl">
        {/* Header */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <div>
              <h1 className="text-2xl font-bold flex items-center gap-2">
                <Package className="h-6 w-6 text-blue-600" />
                รายการอุปกรณ์เบิกจ่าย
              </h1>
              <p className="text-sm text-muted-foreground">Equipment Withdrawal List</p>
            </div>
          </div>
          <Button 
            onClick={handleExportExcel}
            className="bg-green-600 hover:bg-green-700"
            disabled={equipmentList.length === 0}
          >
            <Download className="h-4 w-4 mr-2" />
            ส่งออก Excel
          </Button>
        </div>

        {/* Summary Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                จำนวนรายการทั้งหมด
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-blue-600">
                {equipmentList.length}
              </div>
              <p className="text-xs text-muted-foreground mt-1">รายการ</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                มูลค่ารวมทั้งหมด
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-green-600">
                {calculateTotalCost().toLocaleString()}
              </div>
              <p className="text-xs text-muted-foreground mt-1">บาท</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                รายการที่เสร็จสิ้น
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-purple-600">
                {equipmentList.filter(item => item.status === 2).length}
              </div>
              <p className="text-xs text-muted-foreground mt-1">รายการ</p>
            </CardContent>
          </Card>
        </div>

        {/* Table */}
        <Card>
          <CardHeader>
            <CardTitle>รายการอุปกรณ์</CardTitle>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="text-center py-8 text-muted-foreground">
                กำลังโหลดข้อมูล...
              </div>
            ) : equipmentList.length === 0 ? (
              <div className="text-center py-8 text-muted-foreground">
                ไม่มีรายการอุปกรณ์เบิกจ่าย
              </div>
            ) : (
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead className="w-[80px]">ลำดับ</TableHead>
                      <TableHead>เลขที่รายการ</TableHead>
                      <TableHead>วันที่</TableHead>
                      <TableHead>อุปกรณ์</TableHead>
                      <TableHead className="text-right">ราคา</TableHead>
                      <TableHead>ผู้แจ้ง</TableHead>
                      <TableHead>สาขา</TableHead>
                      <TableHead>ประเภทงาน</TableHead>
                      <TableHead>สถานะ</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {equipmentList.map((item, index) => (
                      <TableRow 
                        key={item.request_id}
                        className="cursor-pointer hover:bg-gray-50"
                        onClick={() => router.push(`/dashboard?ticket=${item.request_id}`)}
                      >
                        <TableCell className="font-medium">{index + 1}</TableCell>
                        <TableCell className="font-mono text-blue-600">
                          {item.request_id}
                        </TableCell>
                        <TableCell>
                          {new Date(item.created_at).toLocaleDateString('th-TH', {
                            year: 'numeric',
                            month: 'short',
                            day: 'numeric'
                          })}
                        </TableCell>
                        <TableCell className="font-medium">
                          {item.description_price}
                        </TableCell>
                        <TableCell className="text-right font-semibold text-green-700">
                          {parseFloat(item.cost).toLocaleString()} ฿
                        </TableCell>
                        <TableCell>{item.username}</TableCell>
                        <TableCell>{item.site || '-'}</TableCell>
                        <TableCell className="text-sm">
                          {item.type_of_work || '-'}
                        </TableCell>
                        <TableCell>
                          <Badge 
                            variant="outline" 
                            className={statusColors[item.status as keyof typeof statusColors]}
                          >
                            {statusText[item.status as keyof typeof statusText]}
                          </Badge>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
